@mixin mobile {
  @media screen and (max-width: 480px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (max-width: 768px) {
    @content;
  }
}
@mixin laptop {
  @media screen and (max-width: 1200px) {
    @content;
  }
}

$Primary: #00799F;
$Secondary: #c9c8c8;

$button: #00485e;
$buttonC: #e6e6e6;

.formContainer {
  background-color: $Primary;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .formWrapper {
    background-color: $Secondary;
    padding: 40px 80px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center;

    .logo {
      color: $Primary;
      font-weight: bold;
      font-size: 24px;

    }

    .title {
      color: $Primary;
      font-size: 12px;
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 15px;

      input {
        padding: 15px;
        border: none;
        border-radius: 10px;
        width: 250px;
        outline:0;
        &::placeholder {
          color: #858585;
        }
      }

      button {
        background-color: $button;
        border-radius: 10px;
        color: $buttonC;
        padding: 10px;
        font-weight: bold;
        border: none;
        cursor: pointer;
      }

      label {
        display: flex;
        align-items: center;
        gap: 10px;
        color: $Primary;
        font-size: 12px;
        cursor: pointer;

        img {
          width: 40px;
        }
      }
    }
    p {
      color: $Primary;
      font-size: 12px;
      margin-top: 10px;
    }
  }
}

.home {
  background-color: $Primary;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .container {
    border-radius: 10px;
    width: 75%;
    height: 80%;
    display: flex;
    overflow: hidden;
    @include tablet {
     width: 85%;
    }

    .sidebar {
      flex: 1;
      background-color: #1E5B81;
      position: relative;
      @include tablet {
        max-width: 8rem;
      }

      .navbar {
        display: flex;
        align-items: center;
        background-color: #003D63;
        height: 50px;
        padding: 10px;
        justify-content: space-between;
        color: #D2FFFF;

        .logo {
          font-weight: bold;
          @include tablet {
            display: none;
          }
        }

        .user {
          display: flex;
          gap: 10px;
          @include tablet {
            
          }
        

          img {
            background-color: #D2FFFF;
            height: 24px;
            width: 24px;
            border-radius: 50%;
            object-fit: cover;
          }

          button {
            background-color: #A0D9EB;
            color: #003D63;
            font-size: 10px;
            border: none;
            border-radius: 6px;
            cursor: pointer;
            @include tablet {
              position: absolute;
              bottom: 10px;
              border-radius: 4px;
            }
          }
        }
      }
      .search {
        border-bottom: 1px solid #96CFE1;;

        .searchForm {
          padding: 10px;
          width: 100%;

          input {
            background-color: transparent;
            border: none;
            color: white;
            outline: 0;

            &::placeholder {
              color: #96CFE1;;
            }
          }
        }
      }

      .userChat {
        padding: 10px;
        display: flex;
        align-items: center;
        gap: 10px;
        color: white;
        cursor: pointer;

        &:hover {
          background-color: #003D63;
        }

        img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
          object-fit: cover;
        }

        .userChatInfo {
          span {
            font-size: 18px;
            font-weight: 500;

            img {
              height: 30px;
            }
          }
          p {
            font-size: 14px;
            color: lightgray;
          }
        }
      }
    }
    .chat {
      flex: 2;

      .chatInfo {
        height: 50px;
        background-color: #1E5B81;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        color: #D2FFFF;

        .leftChat {
          justify-content:flex-start;
          align-items: center;
          display: flex;

          img {
            width: 30px;
            height: auto;
            margin-right: 10px;
          }
        }

      }

      .chatIcons {
        justify-content: flex-end;
        display: flex;
        gap: 10px;

        img {
          height: 24px;
          cursor: pointer;
        }
      }

      .messages {
        background-color: #A0D9EB;
        padding: 10px;
        height: calc(100% - 160px);
        overflow: scroll;
        overflow-x: hidden;

        .message {
          display: flex;
          gap: 20px;
          margin-bottom: 20px;

          .messageInfo {
            display: flex;
            flex-direction: column;
            color: #003D63;
            font-weight: 300;

            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              object-fit: cover;
            }
          }
          .messageContent {
            max-width: 80%;
            display: flex;
            flex-direction: column;
            gap: 10px;

            p {
              background-color: #003D63;
              padding: 10px 20px;
              border-radius: 0px 10px 10px 10px;
              max-width: max-content;
              color: white;
            }

            img {
              width: 50%;
            }
          }

          &.owner {
            flex-direction: row-reverse;

            .messageContent {
              align-items: flex-end;
              p {
                background-color: white;
                color: black;
                border-radius: 10px 0px 10px 10px;
              }
            }
          }
        }
      }

      .input {
        height: 50px;
        background-color: #96CFE1;
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        outline:0;

        input {
          width: 100%;
          border: none;
          outline: none;
          color: #003D63;
          font-size: 18px;
          outline:0;
          background-color: #96CFE1;

          &::placeholder {
            color: #003D63;
          }
        }

        .send {
          display: flex;
          align-items: center;
          gap: 10px;

          img {
            display: flex;
            height: 30px;
            cursor: pointer;
          }

          button {
            border: none;
            border-radius: 5px;
            padding: 10px 15px;
            color: #D2FFFF;
            background-color: $Primary;
            cursor: pointer;
          }
        }
      }
    }
  }
}